body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  justify-content: center;
  padding: 10px;
  text-align: center;
  height: 120vh;
  background-color:#122b2b;
  border-radius: 10px;
}


.symbolsContainer{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.check-box-container{
    display:flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.checkbox{
    width: 20px;
    height: 20px;
    border-radius: 5px;   
}


.chart{
    width: 1100px;
    height: 400px;
    padding: 20px;
    margin-left: 20px;   
}

  .title {
    display: flex;
    justify-content: center;
    align-items: center; 
    border:#56a2a3;
    border-style: solid;
    border-width: 1px;
    border-radius: 10px;
    font-family: Ubuntu;
    font-size: 24px;
    margin: 0;
    height: 50px;
    color: white;
    font-weight: 300;
  }

label{
    margin-right: 10px;
    font-family: "Ubuntu";
    font-size: 20px;   
    color: white;
}

input{
    margin:10px;
    border-radius: 5px;
    padding: 3px 10px;
    font-size: 18px;
    font-family: Ubuntu;
}
select{
    margin:10px;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 18px;
    font-family: Ubuntu;
}
